function tableButtonClick(props, id, text, record, index){

	switch(id){

		case "delline"://删除行
			props.cardTable.delRowsByIndex(tableId, index);
			break;
		case "detail"://更多
			props.cardTable.toggleRowView(tableId, record);
			break;
		case "spread"://展开
			props.cardTable.openModel(tableId, 'edit', record, index);
			break;
		default:
			console.log(id,index);
			break;
	}
}

 //切换页面状态
function toggleShow(props){
	let status = props.getUrlParam('status');
	let flag = status === 'browse' ? false : true;
	//按钮的显示状态
	if(status == 'edit' || status == 'add'){
		props.button.setButtonVisible(['Add','Back'],false);
		props.button.setButtonVisible(['Save','Cancel',],true);
		props.cardPagination.setCardPaginationVisible('cardPaginationBtn', false);
	}else{
		props.button.setButtonVisible(['Save','Cancel'],false);
		props.button.setButtonVisible(['Add','Edit','Delete','Back','Copy'],true);
		props.cardPagination.setCardPaginationVisible('cardPaginationBtn', true);
	}
	props.form.setFormStatus(formId, status);
};

class Card extends Component {
	constructor(props) {
		super(props);
		this.formId = formId;
		//this.searchId = searchId;
       // this.tableId = tableId;
		this.state = {
			title_code : '',
			totalcount : 0,
			applycount : 0,
		}
	}
	componentDidMount() {
		// toggleShow(this.props);
		
		let status = this.props.getUrlParam('status');
		if(status != "add"){
			let	pk = this.props.getUrlParam('id');
			if(pk && pk != 'undefined'){
				this.getdata(pk);
			}
		}
		else{
			this.setDefaultValue();
		}
		
	}

	componentWillReceiveProps(){}

	setDefaultValue = () =>{
		//this.props.form.setFormItemsValue(this.formId,{'pk_org':{value: this.props.CacheTools.get("pk_org"),display:'自由态'}});
	}

    buttonClick =(props, id)=>{

        let _this = this;
        switch (id) {
          case 'Add':
            props.form.EmptyAllFormValue(this.formId)
            //props.cardTable.setTableData(this.tableId, { rows: [] })
            props.linkTo('../card/index.html', {
              status: 'add'
            })
            toggleShow(this.props);
            break
        //   case 'Edit':
        //     props.linkTo('../card/index.html', {
        //       status: 'edit',
        //       id: props.getUrlParam('id')
        //     })
        //     toggleShow(this.props);
        //     break;
        //   case 'delete':
        //     this.props.modal.show('delete');
        //     break
        //   case 'back':
        //     props.linkTo('../list/index.html', {})
        //     break
          case 'Save':
			this.saveClick();
			break
		  case 'Back':
            props.linkTo('../list/index.html', {})
            break
          case 'Cancel':
            if (props.getUrlParam('status') === 'add') {
				props.form.cancel(this.formId);
                props.linkTo('../card/index.html', {
                  status: 'browse'
                })
              
            }
            if ((props.getUrlParam('status') === 'edit')) {
              props.form.cancel(this.formId);
              //props.cardTable.resetTableData(this.tableId);
              props.linkTo('../card/index.html', {
                status: 'browse',
                id: props.getUrlParam('id')
              })
            }
            toggleShow(this.props);
            break
		//   case 'addline':
		// 	let rowNum = props.cardTable.getNumberOfRows(this.tableId);
		// 	props.cardTable.addRow(this.tableId,rowNum,{'showorder':{display:'',value:rowNum+1+''}},false);
        //     break
          case 'refresh':
            props.linkTo('../card/index.html', {
              status:props.getUrlParam('status'),
              id:props.getUrlParam('id')
			})
			toggleShow(this.props);
            toast({content:'刷新成功',color:'success'});
            break
          default:
            break
        }
    }
    
    pageInfoClick=(props, pk)=>{
        
        let data = {
			//'pk_org':cacheTools.get('pk_org'),
            'pk': pk,
			'pageid': pageId
        };
        ajax({
            url: queryCardUrl,
            data: data,
            success: (res) =>{
                if (res.data.head) {
                    
                    props.form.setAllFormValue({ [formId]: res.data.head[formId] });
                    props.setUrlParam(pk)//动态修改地址栏中的id的值
                }
            }
        });
    }


    afterEvent =(props, moduleId, key,value, changedrows, i, s, g)=>{}

	//通过单据id查询单据信息
	getdata = (pk) =>{
		let data = {
			pk
		};
		ajax({
			url: queryCardUrl,
			data,
			success: (res) => {
				if (res.data.head) {
					this.props.form.setAllFormValue({ [this.formId]: res.data.head[this.formId] });
					let title_code = res.data.head[this.formId].rows[0].values[titleCode].value;
					this.setState({title_code});
				}
				
			}
		});
	}

	//保存单据
	saveClick = () =>{
		//this.props.cardTable.filterEmptyRows(tableId);
		let CardData = this.props.createMasterChildData(pageId, this.formId, this.tableId);
		// CardData.head.head.rows[0].values.bill_code = _this.state.bill_code;
		console.log(CardData);
		//console.log(CardData['head']['head']['rows'][0]['values']['fm']);
		CardData['head']['head']['rows'][0]['values']['fm'] = {};
		//console.log(CardData['head']['head']['rows'][0]['values']['fm']);
		let url = saveUrl;//新增保存
		ajax({
			url: url,
			data: CardData,
			success: (res) => {
				let pk_value = null
				if (res.success) {
					if (res.data) {
						if (res.data.head && res.data.head[this.formId]) {
							this.props.form.setAllFormValue({ [this.formId]: res.data.head[this.formId] });
							pk_value = res.data.head[this.formId].rows[0].values[pk_item].value
						}
					}
					toast({content : "保存成功",color : 'success'});
					this.getdata(pk_value);
					this.props.linkTo('../card/index.html', {
						status: 'browse',
						id: pk_value
					})
					toggleShow(this.props)
				}
			}
		})
	  }

	//删除单据
	delConfirm = () => {
		ajax({
			url: deleteUrl,
			data: {
				pk_org: cacheTools.get('pk_org'),
				deleteinfo:[{
				id: this.props.getUrlParam('id'),
				ts: this.props.form.getFormItemsValue(this.formId, 'ts').value
			}]},
			success: (res) => {
				if(res){
					this.props.linkTo('../list/index.html');
				}
				
			}
		});
	};

	modelSave = (props)=>{
		props.cardTable.closeModel(this.tableId);
		this.saveClick();
	}

    getButtonNames = (codeId) => {
		if (codeId === 'edit'||codeId === 'add'||codeId === 'save'){
			return 'main-button'
		}else {
			return 'secondary - button'
		}
	};

	//获取列表肩部信息
	getTableHead = () => {
		let {button} = this.props;
		let { createButtonApp } = button;
		let buttons  = this.props.button.getButtons();
		let status = this.props.getUrlParam("status");
        return (
			<div className="shoulder-definition-area">
				<div className='definition-search'>
					{status == 'browse' ?<div><span className="definition-search-title">详细信息 | 总计：</span>
						<span className = 'count'>{this.state.totalcount}</span><span>条</span>
					{/* <span>		申请数量 ：</span> */}
						{/* <span className='count'>{this.state.applycount}</span><span>个</span> */}
						</div>:<span className="definition-search-title"></span>
					}
				</div>
				<div className="definition-icons">
					{createButtonApp({
						area: 'definition-icons',//按钮注册中的按钮区域
						//buttonLimit: 5, 
						onButtonClick: this.buttonClick.bind(this)
						//popContainer: document.querySelector('.header-button-area')
					})}
					{this.props.cardTable.createBrowseIcons(this.tableId, {
						iconArr: ['close', 'open', 'max','setCol'],
						maxDestAreaId: 'nc-bill-card'
					})}
					{/* {createButton("deleteline", {
						name: '删行',
						onButtonClick: buttonClick.bind(this)
					})} */}
				</div>	
			</div>
        )
    }
	render() {
		let { cardTable, form, button, modal, cardPagination } = this.props;
		const {createCardPagination} = cardPagination;
		let buttons = this.props.button.getButtons();
		buttons = buttons.sort((a,b)=>{
			return b.btnorder - a.btnorder;
		});
		let { createForm } = form;
		let { createCardTable } = cardTable;
		let { createButtonApp } = button;
		let { createModal } = modal;
		let status = this.props.getUrlParam('status');
		return (
			<div  id='nc-bill-card'>
					<div className="nc-bill-card">
						<NCAffix>
							<div className='nc-bill-header-area'>
								<div className='header-title-search-area'>
									<h2 className='title-search-detail'>{titleName}{status=='browse'?`：${this.state.title_code}`:''}</h2>
								</div>
								{/*分页 */}
								{/* {status=='browse'?<div className='header-button-cardPagination'>
									{createCardPagination({	handlePageInfoChange: pageInfoClick.bind(this)})}
									</div>:''} */}
								<div className="header-button-area">
									{createButtonApp({
										area: 'header-button-area',//按钮注册中的按钮区域
										//buttonLimit: 5, 
										onButtonClick: this.buttonClick.bind(this) 
										//popContainer: document.querySelector('.header-button-area')
									})}
									{createCardPagination({
										handlePageInfoChange: this.pageInfoClick.bind(this)
									})}
								</div>
			                </div>
						</NCAffix>
						<div className="nc-bill-form-area">
							{createForm(this.formId, {
								onAfterEvent: this.afterEvent.bind(this)
							})}
						</div>
						{/* <div className="nc-bill-table-area">
							{this.getTableHead()}
							{createCardTable(this.tableId, {
								tableHead: this.getTableHead.bind(this),
								modelSave: this.modelSave.bind(this),
								showIndex:true
							})}
						</div> */}
						{createModal('delete', {
							title: "注意",
							content: '确认删除？',
							beSureBtnClick: this.delConfirm
						})}
					</div>
			</div>
			
		);
	}
}

Card = createPage({
	initTemplate: initTemplate
})(Card);


